import React, { lazy, Suspense, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { updateModal } from "../../projectSlice";
import Draggable from "react-draggable";
import {
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Row,
    Col
} from "reactstrap";

const Project = lazy(() => import("./Project.js"));
const Export = lazy(() => import("./Export.js"));
const About = lazy(() => import("./About.js"));
const Name = lazy(() => import("./Name.js"));
const KeyboardShortcuts = lazy(() => import("./KeyboardShortcuts.js"));

export default function DModal() {
    const dispatch = useDispatch();
    const modal = useSelector(state => state.project.modal);

    const closeModal = () => dispatch(updateModal(null));

    const renderModal = () => {
        switch (modal) {
            case "project":
                    return ["New Project", <Project closeModal={closeModal} />]

            case "export": 
                    return ["Preview", <Export closeModal={closeModal} />]

            case "about":
                    return ["About Dialgo", <About />]
            
            case "name": 
                    return ["Project Name", <Name closeModal={closeModal} />]

            case "shortcuts":
                    return ["Keyboard Shortcuts", <KeyboardShortcuts />]
                
            default: break;
        }
    }

    return (
        <Draggable handle=".modal-header">
            <Modal 
                size={modal === "name" ? "sm" : "lg"}
                isOpen={modal !== null} 
                backdrop={false} 
                fade={false} 
                keyboard={true} 
                centered
            >
                {modal ? 
                <>

                    <ModalHeader toggle={closeModal}>
                        {renderModal()[0]}
                    </ModalHeader>
                    <ModalBody className={(modal === "project" || modal === "export") ? "preview" : ""}>
                        <Row>
                            <Suspense fallback={null}>
                                {renderModal()[1]}
                            </Suspense>
                        </Row>
                    </ModalBody>
                </>
                : null
                }
            </Modal>
        </Draggable>
    );
}